<template>
  <view class="container">
    <view class="input-container">
      <text class="label">请输入你的工资：</text>
      <input
        class="input"
        type="number"
        v-model="salary"
        placeholder="请输入数字"
		@input="handleInput"
      />
    </view>
  </view>
</template>

<script>
import { ref } from "vue";

export default {
  setup() {
    const salary = ref(""); // 用于双向绑定输入框的值
   // 输入事件处理函数
    const handleInput = (event) => {
      const value = parseFloat(event.target.value); // 获取输入值并转换为数字
      if (isNaN(value) || value < 0) {
        // 如果输入值不是数字或小于0，重置为0
        salary.value = 0;
      } else {
        salary.value = value; // 否则更新绑定值
      }
    };
    return {
      salary,
    };
  },
};
</script>

